import { createRouter, createWebHashHistory } from '@ionic/vue-router';
import { RouteRecordRaw } from 'vue-router';
import Inicio from '../views/Inicio.vue';
import InicioAdmin from '../views/InicioAdmin.vue';
import Login from '../views/Login.vue';
import Registro from '../views/Registro.vue';
import ValidarNumero from '../views/ValidarNumero.vue';
import RegistroFinal from '../views/RegistroFinal.vue';
import TabInicio from '../views/TabInicio.vue';
import TabRepartidores from '../views/TabRepartidores.vue';
import TabPedidos from '../views/TabPedidos.vue';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    redirect: '/inicio',
  },
  
  {
    path: '/inicio',
    component: Inicio,
    meta: { requiereAuth: false }
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: { requiereAuth: false }
  },
  {
    path: '/registro',
    name: 'registro',
    component: Registro,
    meta: { requiereAuth: false }
  },
  {
    path: "/validarnumero", 
    name:"validarnumero", 
    component: ValidarNumero,  
    props: true,
    meta: { requiereAuth: false }
  },
  {
    path: "/registrofinal", 
    name:"registrofinal", 
    component: RegistroFinal,  
    props: true,
    meta: { requiereAuth: false }
  },
  {
    path: "/tabs/", 
    name:"inicio-admin", 
    props: true,
    component: InicioAdmin,  
    meta: { requiereAuth: true },
    children: [
      {
        path: '',
        redirect: '/tabs/inicio'
      },
      {
        path: "inicio", 
        name:"tab-inicio", 
        props: true,
        component: TabInicio,  
        meta: { requiereAuth: true }
      },
      {
        path: "repartidores", 
        name:"tab-repartidores", 
        props: true,
        component: TabRepartidores,  
        meta: { requiereAuth: true }
      },
      {
        path: "pedidos", 
        name:"tab-pedidos", 
        props: true,
        component: TabPedidos,  
        meta: { requiereAuth: true }
      },
     
    ]
  }

  
  
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
