<template>
<ion-page>
    <ion-fab horizontal="end" vertical="bottom" slot="fixed" @click="nuevopedido=!nuevopedido">
        <ion-fab-button>
            <ion-icon :icon="nuevopedido ? close : add"></ion-icon>
        </ion-fab-button>
