        <ion-content class="ion-padding">
            <section class="px-3 py-1">

                <div class="w-full sm:w-5/12 mx-auto" v-if="estadoRepartidores.general=='cantidad' && estadoRepartidores.disponibles.length > 0">
                    <span class="block bg-blue-200 text-center font-semibold py-2 px-2 rounded-full mb-3">
                        <span>{{ estadoRepartidores.disponibles.length==1 ? estadoRepartidores.disponibles.length + ' repartidor activo' : estadoRepartidores.disponibles.length +' repartidores activos' }} </span>
                    </span>
                </div>
                <div class="w-full sm:w-5/12 mx-auto" v-if="estadoRepartidores.general=='tiempo'">
                    <span class="block bg-orange-200 text-center font-semibold py-2 px-2 rounded-full mb-3">
                        <span>Repartidor disponible en {{ estadoRepartidores.tiempo }} min</span>
                    </span>
                </div>
                <div class="w-full sm:w-5/12 mx-auto" v-if="estadoRepartidores.general=='cantidad' && estadoRepartidores.disponibles.length == 0">
                    <span class="block bg-red-200 text-center font-semibold py-2 px-2 rounded-full mb-3">
                        <span>Ya no estamos atendiendo</span>
                    </span>
                </div>
                <div class="w-full sm:w-5/12 mx-auto">
                    <div class="block mb-3 py-2 px-3 rounded-lg shadow border border-gray-100">
                        <a @click="nuevopedido=!nuevopedido" href="javascript:void(0);" class="block bg-gray-600 hover:bg-gray-700 text-white rounded-lg py-2 px-4 text-center font-semibold">
                            <span>Nuevo pedido</span>
                        </a>
                        <span v-if="nuevopedido">
                            <div class="mt-4">
                                <form action="#" style="margin-bottom: 6px;">
                                    <div class="mb-2">
                                        <input v-model="form.nombre_tienda" type="text" class="w-full border-2 rounded-lg py-1 px-2 outline-none border-gray-400 focus:border-gray-500" placeholder="Nombre de la tienda">
                                    </div>
