                                        <input v-model="form.coordenadas_tienda" type="text" class="w-full border-2 rounded-lg py-1 px-2 outline-none border-gray-400 focus:border-gray-500" placeholder="Ubicación de la tienda (link maps)">
                                    </div>
                                    <div class="mb-2">
                                        <input v-model="form.coordenadas_cliente" type="text" class="w-full border-2 rounded-lg py-1 px-2 outline-none border-gray-400 focus:border-gray-500" placeholder="Ubicación del cliente (link maps)">
                                    </div>
                                    <div class="mb-2">
                                        <input v-model="form.montodelivery" type="text" class="w-full border-2 rounded-lg py-1 px-2 outline-none border-gray-400 focus:border-gray-500" placeholder="Precio de delivery (incluye propina)">
                                    </div>

                                    <div class="mb-2"><input type="text" v-on:keyup.enter="agregarItems" ref="item" v-model="item" class="w-full border-2 rounded-lg py-1 px-2 outline-none border-gray-400 focus:border-gray-500" placeholder="Producto (cantidad y descripción)"></div>

                                    <a @click="agregarItems" href="javascript:void(0);" class="block bg-red-500 hover:bg-blue-600 text-white rounded-lg py-2 px-2 text-center font-semibold" style="width: 50%; margin-bottom: 8px;">
                                        <span>Añadir items</span>
                                    </a>

                                    <ion-label v-for="(i, index) in items" v-bind:key="index"> - {{ i }} <a href="javascript:void(0);" @click="eliminarItem(index)"> [X]</a><br></ion-label>
                                    <hr class="mb-3">
                                    <div class="mb-2">
                                        <textarea v-model="form.referencia" name="" id="" rows="5" class="w-full border-2 rounded-lg py-1 px-2 outline-none border-gray-400 focus:border-gray-500" placeholder="Ingresa aquí alguna referencia"></textarea>
                                    </div>
                                    <select v-model="form.tipopago" id="cars" class="w-full border-2 rounded-lg py-1 px-2 outline-none border-gray-400 focus:border-gray-500">
                                        <option value="efectivo">Efectivo</option>
                                        <option value="transferencia">Transferencia</option>
                                    </select>

                                </form>
                            </div>
                            <a @click="generarpedido" href="javascript:void(0);" class="block bg-blue-500 hover:bg-blue-600 text-white rounded-lg py-2 px-4 text-center font-semibold">
                                <span>Generar pedido</span>
                            </a>
                        </span>
                    </div>
                    <div class="block mb-3 py-2 px-3 rounded-lg shadow border border-gray-100">
                        <div>
                            <input type="text" v-model="buscar" placeholder="Busca un pedido por id" class="bg-gray-200 rounded-lg py-1 px-3 w-full outline-none font-semibold">
                        </div>
                    </div>
                    <div class="block mb-3 py-2 px-3 rounded-lg shadow border border-gray-100" style="overflow: auto;white-space: pre;">
                        <div class="font-semibold text-lg mb-2">Pedidos en espera</div>
                        <table class="w-full">
                            <thead>
                                <tr class="font-bold">
                                    <td class="py-3 border-b border-gray-400">
                                        Detalles
                                    </td>
                                    <td class="border-b border-gray-400">
                                        Pedido
                                    </td>
                                    <!---
                                    <td class="border-b border-gray-400 text-right">
                                        Producto
                                    </td>
                                    --->
                                    <td class="border-b border-gray-400 text-center">
                                        Delivery
                                    </td>
                                    <td class="border-b border-gray-400 text-center">
                                        Ubicación
                                    </td>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(pedido, index) in pedidosEnEspera" v-bind:key="index">
                                    <td class="py-3 border-b border-gray-400">

                                        <div class="text-lg">
                                            <h3 class="font-semibold inline-block" title="98776534"><a href="#" target="_blank">{{ pedido.nombre_tienda }}</a></h3><br>
                                        </div>
                                        <div class="text-sm text-gray-600">
                                            <span>{{ new Date(pedido.createdAt).toISOString().replace(/T/, ' ').replace(/\..+/, '') }}</span><br>
                                            <span></span><br>
                                        </div>
                                    </td>
                                    <td class="border-b border-gray-400 leading-6">
                                        <span class="font-semibold border-b border-gray-800 border-dotted"><a href="javascript:void(0);">#{{ pedido.pedido_admin_id}}</a></span><br>
                                        <span @click="aceptarpedido(pedido._id)" class="font-semibold border-b border-gray-800 border-dotted text-green-600"><a href="javascript:void(0);">Aceptar</a></span><br>
                                        <span @click="rechazarpedido(pedido._id)" class="font-semibold border-b border-gray-800 border-dotted text-red-600"><a href="javascript:void(0);">Rechazar</a></span><br>
                                    </td>
                                    <!---
                                    <td class="border-b border-gray-400 text-right">
                                        <span class="font-semibold">S/{{ pedido.items.reduce((sum, producto) => sum + (producto.cantidad * producto.precio) , 0) }}</span><br>
                                    </td>
                                    --->
                                    <td class="border-b border-gray-400 text-center">
                                        <span class="font-semibold">S/ {{ pedido.monto_delivery }}</span><br>
                                    </td>
                                    <td class="border-b border-gray-400 text-center mx-2 py-3 leading-5" style="max-width: 5rem;">
                                        <a @click="abrirmapa(pedido.coordenadas_cliente)" href="javascript:void(0);"><i class="fas fa-map-marker-alt"></i><br><span>Abrir mapa</span></a>

                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="block mb-3 py-2 px-3 rounded-lg shadow border border-gray-100">
                        <hr>
                    </div>
                    <div class="block mb-3 py-2 px-3 rounded-lg shadow border border-gray-100" style="overflow: auto;white-space: pre;">
                        <div class="font-semibold text-lg mb-2">Pedidos en curso</div>
                        <table class="w-full">
                            <thead>
                                <tr class="font-bold">
                                    <td class="py-3 border-b border-gray-400">
                                        Detalles
                                    </td>
                                    <td class="border-b border-gray-400">
                                        Pedido
                                    </td>
                                    <!---
                                    <td class="border-b border-gray-400 text-right">
                                        Producto
                                    </td>
                                    --->
                                    <td class="border-b border-gray-400 text-center">
                                        Delivery
                                    </td>
                                    <td class="border-b border-gray-400 text-center">
                                        Ubicación
                                    </td>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(pedido, index) in pedidosEnCurso" v-bind:key="index">
                                    <td class="py-3 border-b border-gray-400">

                                        <div class="text-lg">
                                            <h3 class="font-semibold inline-block"><a href="#" target="_blank" class="border-b border-gray-800 border-dotted">{{ pedido.repartidor ? pedido.repartidor.nombres + ' ' + pedido.repartidor.apellidos : 'Buscando repartidor' }}</a></h3><br>
                                            <span title="98776534">{{ pedido.nombre_tienda }}</span><br>
                                        </div>
                                        <!---
                                        <div class="text-sm text-gray-600">
                                            <span>G 12:00pm</span> · <span>A: 12:00pm</span><br>
                                        </div>
                                        --->
                                        <div class="text-gray-600">
                                            <span v-if="pedido.estado == 'en curso'">0️⃣ Pedido confirmado</span><br>
                                            <span v-if="pedido.estado == 'repartidor asignado'">0️⃣0️⃣ Repartidor asignado<br></span>
                                            <span v-if="pedido.estado == 'llegue a tienda'">1️⃣ Repartidor llegó a tienda<br></span>
                                            <span v-if="pedido.estado == 'tengo el pedido'">2️⃣ Repartidor en camino donde cliente<br></span>
                                            <span v-if="pedido.estado == 'llegue al destino'">3️⃣ Repartidor llegó donde el cliente<br></span>
                                            <span v-if="pedido.estado == 'entregue el pedido'">4️⃣ Repartidor ya entregó el pedido<br></span>
                                        </div>
                                    </td>
                                    <td class="border-b border-gray-400">
                                        <span class="font-semibold border-b border-gray-800 border-dotted"><a href="#">#{{ pedido.pedido_admin_id}}</a></span><br>
                                    </td>
                                    <!---
                                    <td class="border-b border-gray-400 text-right">
                                        <span class="font-semibold">S/{{ pedido.items.reduce((sum, producto) => sum + (producto.cantidad * producto.precio) , 0) }}</span><br>
                                    </td>
                                    --->
                                    <td class="border-b border-gray-400 text-center">
                                        <span class="font-semibold">S/{{ pedido.monto_delivery}}</span><br>
                                    </td>
                                    <td class="border-b border-gray-400 text-center mx-2 leading-5 py-3" style="max-width: 5rem;">
                                        <a @click="abrirmapa(pedido.coordenadas_cliente)" href="javascript:void(0);"><i class="fas fa-map-marker-alt"></i><br><span>Mapa cliente</span></a>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <div class="h-20">

                </div>
            </section>
        </ion-content>
    </div>

</ion-page>
</template>

<script>
import {

    IonContent,
    IonPage,
    IonFab,
    IonFabButton,
    IonTitle,
    IonToolbar,
    IonHeader,
    IonMenuButton,
    IonButtons,
    loadingController,

    modalController,
    alertController

} from '@ionic/vue';
import {
    add,
    close
} from 'ionicons/icons';
//import CargandoMixin from '../views/mixins/cargando';
import AlertaMixin from '../views/mixins/alerta';
import TiempoDemora from '../views/TiempoDemora';

export default {
    name: 'TabInicio',
    mixins: [AlertaMixin],
    components: {
        IonFab,
        IonFabButton,
        IonPage,
        IonContent,
        IonMenuButton,
        IonButtons,

        IonTitle,
        IonToolbar,
        IonHeader,

    },
    setup() {
        return {
            add,
            close
        }
    },
    data() {
        return {
            loading: null,
            buscar: '',
            modal: '',
            isOpen: false,
            nuevopedido: false,
            form: {
                estado: 'en espera',
                tipopago: 'efectivo'
            },
            estadoPedido: 0,
            tipo: 0,
            estadoRepartidores: {},
            items: [],
            item: '',
        }

    },
    async created() {
        this.abrirCargando('Cargando...', 'my-custom-loading')
        this.fcm.getToken({
            vapidKey: 'BMvK2Y2H_W_Xx_-m_ZQ8UQBb4YikCxU--h17guryPJTJRZf4Hp9lyes-pGmJ6oJYJg8-WzVyy168eY_3xwAa2-c'
        }).then((currentToken) => {
            if (currentToken) {
                console.log(currentToken)
                this.$http.post("https://app.jamuy.net/api/registrar-token-admin", {
                        token: currentToken
                    })
                    .then(response => {
                        console.log(response)
                        this.cerrarCargando()
                    }).catch((error) => {
                        console.log(error)
                        this.cerrarCargando()
                    });

            } else {
                // Show permission request UI
                console.log('No registration token available. Request permission to generate one.');
                // ...
            }
        }).catch((err) => {
            console.log('An error occurred while retrieving token. ', err);
            // ...
        });

        this.$http.get("https://app.jamuy.net/api/lista-repartidores-disponibles")
            .then(response => {
                this.tipo = 0
                this.repartidores = response.data.detalles
            }).catch((error) => {
                console.log(error)
            });

        this.estadoRepartidoresAvisos()

    },
    mounted() {

        this.fcm.onTokenRefresh((nuevo) => {
            console.log('REFRESH:', nuevo)
            this.$http.post("https://app.jamuy.net/api/registrar-token-admin", {
                    token: nuevo
                })
                .then(response => {
                    console.log(response)
                }).catch((error) => {
                    console.log(error)
                });
        });

        navigator.serviceWorker.addEventListener('message', (event) => {

            console.log('RECIBIDO DEL SERVICE WORKER ', event.data);

            const payload = JSON.parse(event.data.data.detalles)
            if (event.data.data.valor == '1') {
                this.tipo = 1
                this.repartidores = payload
            } else if (event.data.data.valor == '-1') {
                this.tipo = 2
                this.repartidores = payload
            }
            else if (event.data.data.valor == "10") {
                this.estadoPedido = 1
                this.pedidos = payload
            }
            else if (event.data.data.valor == "11") {
                this.estadoPedido = 2
                this.pedidos = payload
            }
            else if (event.data.data.valor == "12") {
                this.estadoPedido = 3
                this.pedidos = payload
            }
            else if (event.data.data.valor == "0") {
                this.estadoPedido = 3
                this.pedidos = payload
            } else if (event.data.data.valor == "4") {
                this.estadoPedido = 4
                this.pedidos = payload
            } else if (event.data.data.valor == "5") {
                this.estadoPedido = 5
                this.pedidos = payload
            } else if (event.data.data.valor == "6") {
                this.tipo = 3
                this.repartidores = payload
            }

        });

        /*
        this.fcm.onMessage((payload) => {
            console.log('Message received. ', payload);

            if (payload.data.valor == '1') {
                this.tipo = 1
                this.repartidores = JSON.parse(payload.data.detalles)
            } else if (payload.data.valor == '-1') {
                this.tipo = 2
                this.repartidores = JSON.parse(payload.data.detalles)
            } else if (payload.data.valor == "0") {
                this.estadoPedido = 3
                this.pedidos = JSON.parse(payload.data.detalles)
            } else if (payload.data.valor == "4") {
                this.estadoPedido = 4
                this.pedidos = JSON.parse(payload.data.detalles)
            } else if (payload.data.valor == "5") {
                this.estadoPedido = 5
                this.pedidos = JSON.parse(payload.data.detalles)
            } else if (payload.data.valor == "6") {
                this.tipo = 3
                this.repartidores = JSON.parse(payload.data.detalles)
            }
        });
        */
    },

    computed: {

        pedidos: {
            get() {
                return this.$store.state.pedidos;
            },
            set(value) {
                if (this.estadoPedido == 1)
                    this.$store.commit("setnuevopedido", value)
                else if (this.estadoPedido == 2)
                    this.$store.commit("setpedidoaceptado", value)
                else if (this.estadoPedido == 3)
                    this.$store.commit("setpedidorechazado", value)
                else if (this.estadoPedido == 0)
                    this.$store.commit("setpedidos", value)
                else if (this.estadoPedido == 4)
                    this.$store.commit("setpedidorepartidor", value)
                else if (this.estadoPedido == 5)
                    this.$store.commit("setactualizacionpedido", value)

            }
        },
        repartidores: {
            get() {
                return this.$store.state.repartidores;
            },
            set(value) {
                if (this.tipo == 0) {
                    this.$store.commit("setlistarepartidorest", value)
                } else if (this.tipo == 1) {
                    this.$store.commit("setlistarepartidores", value)
                } else if (this.tipo == 3) {
                    this.$store.commit("setlistarepartidoresu", value)
                } else {
                    this.$store.commit("setlistarepartidoresd", value)
                }
                this.tipo = 0
                this.estadoRepartidoresAvisos()
            }
        },
        pedidosEnEspera() {
            const bus = this.buscar
            return this.pedidos.filter(function (item) {
                return item.estado == 'en espera' && item.pedido_admin_id.includes(bus)
            });
        },
        pedidosEnCurso() {
            const bus = this.buscar
            return this.pedidos.filter(function (item) {
                return (item.estado !== 'en espera' && item.estado !== 'rechazado' && item.estado !== 'entregue el pedido') && item.pedido_admin_id.includes(bus)
            });
        },

    },

    methods: {
        async abrirCargando(mensaje, clase) {
            this.loading = await loadingController
                .create({
                    spinner: 'circles',
                    duration: 0,
                    message: mensaje,
                    translucent: true,
                    cssClass: clase,
                    backdropDismiss: false,
                    mode: 'ios',
                    animated: true,
                    keyboardClose: false
                });

            await this.loading.present();
        },
        cerrarCargando() {
            if (this.loading != null) {
                this.loading.dismiss()
            }
        },
        async createModal(id) {
            this.modal = await modalController.create({
                component: TiempoDemora,
                swipeToClose: true,
                mode: 'ios',
                cssClass: 'modal-tiempo-demora',
                backdropDismiss: true,
                componentProps: {
                    title: 'Tiempo de demora',
                    id: id,
                    cerrar: () => this.closeModal()
                },

            })
        },
        async openModal(id) {
            await this.createModal(id)
            this.isOpen = true
            this.modal.present()
        },
        closeModal() {
            this.isOpen = false
            this.modal.dismiss().then(() => {
                this.modal = null;
            });
        },
        abrirmapa(c) {
            window.open(c)
        },
        agregarItems() {
            this.items.push(this.item)
            this.item = ''
            this.$refs.item.focus();
        },
        eliminarItem(i) {
            this.items.splice(i, 1)
        },
        estadoRepartidoresAvisos() {
            this.estadoRepartidores.disponibles = this.repartidores.filter(function (item) {
                return item.cola.length == 0
            });

            this.estadoRepartidores.activos = this.repartidores.length
            if (this.estadoRepartidores.disponibles.length == 0 && this.estadoRepartidores.activos > 0) {
                this.estadoRepartidores.general = 'tiempo'
                this.estadoRepartidores.tiempo = 0
                for (const pedido of this.repartidores["0"].cola) {

                    if (this.repartidores["0"].cola.length == 1) {
                        this.estadoRepartidores.tiempo = pedido.tiempo
                    } else {
                        const adicional = (25 * this.repartidores["0"].cola.length)
                        this.estadoRepartidores.tiempo = pedido.tiempo + this.estadoRepartidores.tiempo + adicional
                    }
                }
            } else {
                this.estadoRepartidores.general = 'cantidad'
            }

        },
        generarpedido() {
            this.abrirCargando('Generando nuevo pedido', 'my-custom-loading')
            this.form.items = this.items
            this.$http.post("https://app.jamuy.net/api/registrar-pedido", this.form)
                .then((response) => {
                    this.cerrarCargando()
                    //this.estadoPedido = 1
                    //this.pedidos = response.data.detalles
                    this.nuevopedido = false
                    this.form = {
                        estado: 'en espera',
                        tipopago: 'efectivo'
                    }
                    this.items = []

                })
                .catch((error) => {
                    this.cerrarCargando()
                    if (error.response.status == 422) {
                        this.abrirNotificacion("Error", error.response.data.error, 'middle', 1000, 'toast-login')
                    }
                });
        },

        aceptarpedido(id) {
            this.openModal(id)
        },
        async rechazarpedido(id) {
            const alert = await alertController
                .create({
                    cssClass: 'mi-alerta',
                    header: '¿Seguro de rechazar el pedido?',
                    message: 'Si oprimes aceptar, el pedido no se procesará.',
                    buttons: [{
                            text: 'No',
                            role: 'cancel',
                            cssClass: 'secondary',
                        },
                        {
                            text: 'Sí',
                            handler: () => {
                                this.abrirCargando('Rechazando pedido...', 'my-custom-loading')
                                this.$http.post("https://app.jamuy.net/api/rechazar-pedido", {
                                        id: id
                                    })
                                    .then((response) => {
                                        this.estadoPedido = 3
                                        this.pedidos = response.data.detalles
                                        this.cerrarCargando()
                                    })
                                    .catch((error) => {
                                        if (this.loading != null) {
                                            this.cerrarCargando()
                                        }
                                    });
                            }
                        },
                    ],
                })
            return alert.present();
        },
    },

}
</script>
