<template>
<ion-page>
    <ion-header translucent>
        <ion-toolbar>
            <ion-title>{{ title }}</ion-title>
            <ion-buttons slot="end">
                <ion-button @click="cerrarmodal">Cerrar</ion-button>
            </ion-buttons>
        </ion-toolbar>
    </ion-header>

    <ion-content>

        <div class="block mb-3 py-1 px-3 rounded-lg shadow border border-gray-100 bg-blue-500 text-white">
            <span class="font-bold text-xl">¿Cuánto tiempo te tomará?</span><br>
            <div class="text-center my-5">
                <a @click="tiempodemora=parseInt(tiempodemora)-5" href="javascript:void(0);" class="inline-block text-red-600 mr-5"><i class="fas fa-minus fa-2x"></i></a>
                <span class="font-bold text-4xl">{{ tiempodemora }}</span>
                <span class="font-bold text-2xl">min</span>
                <a @click="tiempodemora=parseInt(tiempodemora)+5" href="javascript:void(0);" class="inline-block text-blue-700 ml-5"><i class="fas fa-plus fa-2x"></i></a><br>
                <label class="cursor-pointer"><input type="checkbox" @change="guardar($event)" v-model="opcion" class="mr-2"><span>Guardar tiempo</span></label>
            </div>
            <div class="mb-3 leading-4">
                <span class="text-xs">Se le asignará su repartidor faltando 5min o 10min de su tiempo de preparación.</span><br>
            </div>
        </div>
        <ion-button @click="aceptarpedido()" color="primary" expand="block">Aceptar pedido</ion-button>

    </ion-content>

</ion-page>
</template>

<script>
import {
    IonButtons,
    IonContent,
    IonButton,
    IonToolbar,
    IonHeader,
    IonTitle,

    IonPage
} from '@ionic/vue';
import {
    defineComponent
} from 'vue';
import CargandoMixin from '../views/mixins/cargando';

export default defineComponent({
    name: 'tiempo-demora',
    mixins: [CargandoMixin],
    props: {
        title: {
            type: String,
            default: 'Super Modal'
        },
        id: {
            type: String,
        },
        cerrar: {
            type: Function
        },
    },
    data() {
        return {
            content: 'Content',
            opcion: false,
            tiempodemora: 10,
            estadopedido: 0
        }
    },
    created() {
        this.tiempodemora = this.tiempo
    },
    computed: {
        tiempo: {
            get() {
                return this.$store.state.tiempo;
            },
            set(value) {
                this.$store.commit("settiempo", value)
            }
        },
        pedidos: {
            get() {
                return this.$store.state.pedidos;
            },
            set(value) {
                if (this.estadoPedido == 1)
                    this.$store.commit("setnuevopedido", value)
                else if (this.estadoPedido == 2)
                    this.$store.commit("setpedidoaceptado", value)
                else if (this.estadoPedido == 3)
                    this.$store.commit("setpedidorechazado", value)
                else if (this.estadoPedido == 0)
                    this.$store.commit("setpedidos", value)
                else if (this.estadoPedido == 4)
                    this.$store.commit("setpedidorepartidor", value)
                else if (this.estadoPedido == 5)
                    this.$store.commit("setactualizacionpedido", value)

            }
        },
    },
    methods: {
        cerrarmodal() {
            this.cerrar()

        },
        guardar(event) {
            if (event.target.value) {
                this.tiempo = this.tiempodemora
            }
        },
        aceptarpedido() {
            this.abrirCargando('Aceptando pedido...', 'my-custom-loading')
            this.$http.post("https://app.jamuy.net/api/aceptar-pedido", {
                    id: this.id,
                    tiempodemora: this.tiempodemora
                })
                .then((response) => {
                    this.estadoPedido = 2
                    this.pedidos = response.data.detalles
                    this.cerrarCargando()
                    this.cerrarmodal()
                })
                .catch((error) => {
                    this.cerrarCargando()
                    this.cerrarmodal()
                });
        },

    },
    components: {
        IonButton,
        IonButtons,
        IonToolbar,
        IonPage,
        IonHeader,
        IonTitle,
        IonContent
    },
});
</script>
