import { createStore } from "vuex";
const repartidores = window.localStorage.getItem('repartidores');
const pedidos = window.localStorage.getItem('pedidos');
const pedidosadmin = window.localStorage.getItem('pedidosadmin');
const tiempo = window.localStorage.getItem('tiempo');
const token = window.localStorage.getItem('token');

export const store = createStore({
  state: {
      token: token,
      pedidos: pedidos ? JSON.parse(pedidos) : [],
      tiempo: tiempo ? tiempo : 10,
      pedidosadmin: pedidosadmin ? JSON.parse(pedidosadmin) : [],
      repartidores: repartidores ? JSON.parse(repartidores) : [],
  },
  getters: {
    loggedIn(state) {
      return state.token !== null
    },
    Whattoken(state) {
      return state.token
    },
  },
  mutations: {
    // Repartidores
    settiempo(state,data) {
      state.tiempo = data
      window.localStorage.setItem('tiempo', JSON.stringify(state.tiempo));
    },
    setlistarepartidores(state, data) {
      state.repartidores.push(data)
      window.localStorage.setItem('repartidores', JSON.stringify(state.repartidores));
    },
    setlistarepartidorest(state, data) {
      state.repartidores = data
      window.localStorage.setItem('repartidores', JSON.stringify(state.repartidores));
    },
    setlistarepartidoresd(state, data) {
      const index = state.repartidores.findIndex(r => r.id == data.id);
      state.repartidores.splice(index, 1);
      window.localStorage.setItem('repartidores', JSON.stringify(state.repartidores));
    },
    setlistarepartidoresu(state,data) {
      const registro = state.repartidores.find(r => r._id == data._id);
      if(registro) {
        registro.cola = data.cola
      }
      window.localStorage.setItem('repartidores', JSON.stringify(state.repartidores));
    },
    setlistatotalpedidosadmin(state, data) {
      state.pedidosadmin = data
      window.localStorage.setItem('pedidosadmin', JSON.stringify(state.pedidosadmin));
    },
    setnuevopedidoaceptadoadmin(state, data) {
      state.pedidosadmin.push(data)
      window.localStorage.setItem('pedidosadmin', JSON.stringify(state.pedidosadmin));
    },
    setnuedopedidorechazadoadmin(state, data) {
      state.pedidosadmin.push(data)
      window.localStorage.setItem('pedidosadmin', JSON.stringify(state.pedidosadmin));
    },
    setactualizacionpedidoadmin(state,data)  {
      const registro = state.pedidosadmin.find(r => r._id == data._id);
      if(registro) {
        registro.estado = data.estado
      }
      window.localStorage.setItem('pedidosadmin', JSON.stringify(state.pedidosadmin)); 
    },
    setpedidos(state, data) {
      state.pedidos = data
      window.localStorage.setItem('pedidos', JSON.stringify(state.pedidos));
    },
    setnuevopedido(state, data) {
      state.pedidos.push(data)
      window.localStorage.setItem('pedidos', JSON.stringify(state.pedidos));
    },
    setpedidorechazado(state, data) {
      const registro = state.pedidos.find(r => r._id == data._id);
      if(registro) {
        registro.estado = 'rechazado'
      }
      window.localStorage.setItem('pedidos', JSON.stringify(state.pedidos));
    },
    setpedidoaceptado(state, data) {
      console.log(state.pedidos)
      console.log(data)
      const registro = state.pedidos.find(r => r._id == data._id);
      if(registro) {
        registro.estado = 'en curso'
      }
      window.localStorage.setItem('pedidos', JSON.stringify(state.pedidos));
    },
    setpedidorepartidor(state,data) {
      console.log(data)
      const registro = state.pedidos.find(r => r._id == data._id);
      if(registro) {
        registro.repartidor = data.repartidor
        registro.estado = 'repartidor asignado'
      }
      window.localStorage.setItem('pedidos', JSON.stringify(state.pedidos)); 
    },
    setactualizacionpedido(state,data)  {
      const registro = state.pedidos.find(r => r._id == data._id);
      if(registro) {
        registro.estado = data.estado
      }
      window.localStorage.setItem('pedidos', JSON.stringify(state.pedidos)); 
    },
    retrieveToken(state, token) {
      state.token = token
    },
    destroyToken(state) {
      state.token = null
    }


  },
  actions: {
    retrieveToken(context, credentials) {

      return new Promise((resolve, reject) => {
        window.axios.post('https://app.jamuy.net/api/auth/ingresar',  credentials )
          .then(response => {
            const token = response.data.token
            localStorage.setItem('token', token)
            context.commit('retrieveToken', token)
            window.axios.defaults.headers.common['Authorization'] = context.state.token;
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      })

    },
    registro(context, credentials) {

      return new Promise((resolve, reject) => {
        window.axios.post('https://app.jamuy.net/api/auth/registro',  credentials )
          .then(response => {
            const token = response.data.token
            window.localStorage.setItem('token', token)
            context.commit('retrieveToken', token)
            window.axios.defaults.headers.common['Authorization'] = context.state.token;
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      })

    },
    destroyToken(context) {
      
      if (context.getters.loggedIn){
        
        return new Promise((resolve, reject) => {
          window.axios.post('https://app.jamuy.net/api/auth/salir', '', {
              headers: { Authorization: "Bearer " + context.state.token }
            })
            .then(response => {
              localStorage.removeItem('token')
              context.commit('destroyToken')
              resolve(response)
            })
            .catch(error => {
              localStorage.removeItem('token')
              context.commit('destroyToken')

              reject(error)
            })
        })

      }
    }
  }

});

