import { createApp } from 'vue'
import App from './App.vue'
import router from './router';

import { IonicVue } from '@ionic/vue'; 
import './assets/styles/tailwind.css';


//import { MazPhoneNumberInput } from '../node_modules/maz-ui/lib/maz-phone-number-input'
import '@ionic/core/css/ionic.bundle.css';

/* Core CSS required for Ionic components to work properly */
import '@ionic/vue/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/vue/css/normalize.css';
import '@ionic/vue/css/structure.css';
import '@ionic/vue/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/vue/css/padding.css';
import '@ionic/vue/css/float-elements.css';
import '@ionic/vue/css/text-alignment.css';
import '@ionic/vue/css/text-transformation.css';
import '@ionic/vue/css/flex-utils.css';
import '@ionic/vue/css/display.css';

import axios from 'axios'
import VueAxios from 'vue-axios'
import { store } from "./store";

//import { FCM } from "cordova-plugin-fcm-with-dependecy-updated/ionic";
import FCM from 'firebase';
/* Theme variables */
import './theme/variables.css';

declare global {
  interface Window {
    axios: any;
    //fcm: any;
  }
}
  
const app = createApp(App)
  .use(IonicVue)
  .use(VueAxios, axios)
  .use(store)

  .use(router);

  const config = {
    apiKey: "AIzaSyAfG2jGc3KWT5Ud5TP5cNdMPEece-EWuwk",
    authDomain: "jamuy-ab9c5.firebaseapp.com",
    databaseURL: "https://jamuy-ab9c5-default-rtdb.firebaseio.com",
    projectId: "jamuy-ab9c5",
    storageBucket: "jamuy-ab9c5.appspot.com",
    messagingSenderId: "663307726242",
    appId: "1:663307726242:web:1782b82871ed91731a56fb",
    measurementId: "G-V3QCHXVVFK"
  };

  FCM.initializeApp(config)

  app.config.globalProperties.fcm =  FCM.messaging()
  
  window.axios = require('axios');

  window.axios.interceptors.request.use(
    (request) => {
      const token = store.getters.Whattoken;
	if(token) {
		request.headers.Authorization = token;
	}
      return request;
    }
  );

  //window.axios.defaults.headers.common['Authorization'] = store.state.token;
  
  window.axios.defaults.headers.common['Content-Type'] = 'application/x-www-form-urlencoded'
  window.axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';

  router.beforeEach(async (to, from, next) => {


  if ((to.name === 'login' || to.name === 'registro') && store.getters.loggedIn) { next({ path: '/tabs/' }) }

  if (to.matched.some(record => record.meta.requiereAuth)) {
    
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    if (!store.getters.loggedIn) {
      next({
        path: '/login',
        query: { redirect: to.fullPath }
      })
    }
    else {  
     next()
    }
  } else {
    next() // make sure to always call next()!
  }
})
  
  
router.isReady().then(() => {
  app.mount('#app');
});

