<template>
<ion-page>
    <ion-header translucent>
        <ion-toolbar>
            <ion-title>{{ title }}</ion-title>
            <ion-buttons slot="end">
                <ion-button @click="cerrarmodal">Cerrar</ion-button>
            </ion-buttons>
        </ion-toolbar>
    </ion-header>

    <ion-content>

        <ion-list>

            <ion-item>
                <ion-label position="stacked">Email o celular</ion-label>
                <ion-input v-model="form.usuario" type="text"></ion-input>
            </ion-item>

            <ion-item>
                <ion-label position="stacked">Contraseña</ion-label>
                <ion-input v-model="form.clave" type="password"></ion-input>
            </ion-item>

        </ion-list>

        <ion-button @click="ingresar" color="primary" expand="block">Ingresar</ion-button>

    </ion-content>

</ion-page>
</template>

<script>
import {
    IonButtons,
    IonContent,
    IonButton,
    IonToolbar,
    IonHeader,
    IonTitle,
    IonList,
    IonLabel,
    IonInput,
    IonItem,
    loadingController,
    IonPage
} from '@ionic/vue';
//import CargandoMixin from '../views/mixins/cargando';
import AlertaMixin from '../views/mixins/alerta';

import {
    defineComponent
} from 'vue';

export default defineComponent({
    name: 'login',
    mixins: [AlertaMixin],
    props: {
        title: {
            type: String,
            default: 'Super Modal'
        },
        cerrar: {
            type: Function
        }
    },
    data() {
        return {
            content: 'Content',
            form: {
                usuario: '',
                clave: '',
            },
            loading: null
        }
    },
    methods: {
        async abrirCargando(mensaje,clase) { 
        this.loading = await loadingController
        .create({
          spinner: 'circles',
          duration: 0,
          message: mensaje,
          translucent: true,
          cssClass: clase,
          backdropDismiss: false,
          mode: 'ios',
          animated: true,
          keyboardClose: false
        });

      await this.loading.present();
     },
     cerrarCargando() {
            if(this.loading!=null) {
            this.loading.dismiss()
            }
      
     },
        cerrarmodal() {
            this.cerrar()

        },
        ingresar() {
            this.abrirCargando('Iniciando sesión', 'my-custom-loading')
            this.$store
                .dispatch("retrieveToken", this.form)
                .then(response => {
                    this.cerrarCargando()
                    this.cerrarmodal()
                    this.$router.push('/tabs/')

                })
                .catch(error => {
                    console.log('whats app:',error)
                    this.cerrarCargando()
                    if (error.response.status == 401) {
                        this.abrirNotificacion("Error", 'Datos incorrectos.', 'top', 1000, 'toast-login')
                    }
                });

        },

    },
    components: {
        IonButton,
        IonButtons,
        IonToolbar,
        IonList,
        IonInput,
        IonLabel,
        IonItem,
        IonPage,
        IonHeader,
        IonTitle,
        IonContent
    },
});
</script>
