<template>
<ion-page>
    <ion-header>
            <ion-toolbar>
                <ion-buttons slot="start">
                    <ion-menu-button></ion-menu-button>
                </ion-buttons>
                <ion-title>Repartidores</ion-title>
            </ion-toolbar>
