        <ion-content class="ion-padding">
    <section class="px-3 py-1">      
        <div class="w-full sm:w-5/12 mx-auto">
            <div class="block mb-3 py-2 px-3 rounded-lg shadow border border-gray-100">
                <div>
                    <input type="text" v-model="buscar" placeholder="Busca repartidor" class="bg-gray-200 rounded-lg py-1 px-3 w-full outline-none font-semibold">
                </div>
            </div>
            <div class="block mb-3 py-2 px-3 rounded-lg shadow border border-gray-100">
                <table class="w-full">
                    <thead>
                        <tr class="font-bold">
                            <td class="py-3 border-b border-gray-400">
                                Nombre
                            </td>
                            <td class="border-b border-gray-400">
                                Id                                 
                            </td>
                            <td class="border-b border-gray-400 text-center">
                                Inició                                 
                            </td>
                            <td class="border-b border-gray-400 text-center">
                                U. conexión                                 
                            </td>
                        </tr>  
                    </thead>
                    <tbody>

                        
                      
                        <tr v-for="(repartidor, index) in repa" v-bind:key="index">
                            <td class="py-3 border-b border-gray-400">
                                <div>
                                    <h3 class="font-semibold inline-block" title="98776534"><a href="javascript:void(0);" target="_blank" class=" border-b border-gray-800 border-dotted">{{ repartidor.id.nombres + ' ' + repartidor.id.apellidos }}</a></h3><br>
                                </div>
                                <div class="text-sm text-gray-600">
                                    <span v-if="repartidor.id.documentoidentidad">DNI: {{ repartidor.id.documentoidentidad}}</span><br>
                                    <span v-if="repartidor.id.movil">CEL: {{ repartidor.id.movil }}</span><br>
                                </div>                                
                            </td>
                            <td class="border-b border-gray-400 leading-6">
                                <span class="font-semibold">#{{ repartidor.id.usuario_id}}</span><br>
                                <!---
                                <span @click="cambiarEstado(true, repartidor)" class="font-semibold border-b border-gray-800 border-dotted text-green-600"><a href="javascript:void(0);">Activo</a></span><br>
                                --->
                                <span @click="cambiarEstado(false, repartidor)" class="font-semibold border-b border-gray-800 border-dotted text-red-600"><a href="javascript:void(0);">Desconectar</a></span><br>
                            </td>
                            <td class="border-b border-gray-400 text-center">
                                <div class="text-sm text-gray-600">
                                    <span>{{ new Date(repartidor.id.createdAt).toISOString().replace(/T/, ' ').replace(/\..+/, '') }}</span><br>
                                </div>
                            </td>
                            <td class="border-b border-gray-400 text-center">
                                <div class="text-sm text-gray-600">
                                    <span>{{ new Date(repartidor.createdAt).toISOString().replace(/T/, ' ').replace(/\..+/, '') }}</span><br>
                                </div>
                            </td>
                        </tr>
                       
                    </tbody>
                </table>
            </div>            
        </div>
        <div class="h-20">

        </div>
    </section>
        </ion-content>
</ion-page>
</template>
<script>
import {
    IonPage,
    IonContent,
    IonHeader,
    IonToolbar,
    IonButtons,
    IonMenuButton,
    IonTitle
} from '@ionic/vue';
export default {
    name: 'tab-repartidores',
    components: {
        IonPage,
        IonContent,
    IonHeader,
    IonToolbar,
    IonButtons,
    IonMenuButton,
    IonTitle
    },
    data() {
        return {
            tipo: 0,
            buscar: ''
        }
    },
    created() {
        /*
        this.$soketio.on('mostrar-lista-repartidores', (data) => {
            this.tipo = 0
            this.repartidores = data
        });
        this.$soketio.on('agregar-repartidor-lista', (data) => {
            this.tipo = 1
            this.repartidores = data
        });
        this.$soketio.on('quitar-repartidor-lista', (data) => {
            this.tipo = 2
            this.repartidores = data
        });
        */
    },
    methods: {
        cambiarEstado(estado, repartidor) {
            this.$http.post("https://app.jamuy.net/api/cambiar-estado-repartidor-admin", {
                        estado: estado,
                        socketid: repartidor.socket_id
                    })
                    .then(async (response) => {
                        console.log(response)
                    }).catch((error) => {
                        console.log(error)
                    });
        }  
    },
    computed: {
        repa() {
            const bus = this.buscar
            return this.repartidores.filter(function (item) {
                return item && item.id.usuario_id.includes(bus)
            });
        },
        repartidores: {
            get() {
                return this.$store.state.repartidores;
            },
            set(value) {
                if (this.tipo == 0) {
                    this.$store.commit("setlistarepartidorest",  value)
                }
                else if(this.tipo == 1) {
                    this.$store.commit("setlistarepartidores", value)   
                }
                else {
                    this.$store.commit("setlistarepartidoresd", value)
                }
                this.tipo = 0
            }
        },
    }
}
</script>