<template>
<ion-page>
    <ion-tabs>
        <ion-tab-bar slot="bottom">
            <ion-tab-button tab="inicio" href="/tabs/inicio">
                <ion-icon :icon="home"></ion-icon>
                <ion-label>Inicio</ion-label>
            </ion-tab-button>
            <ion-tab-button tab="repartidores" href="/tabs/repartidores">
                <ion-icon :icon="bicycle"></ion-icon>
                <ion-label>Repartidores</ion-label>
                <ion-badge>{{ repartidores.length }}</ion-badge>
            </ion-tab-button>
            <ion-tab-button tab="pedidos" href="/tabs/pedidos">
                <ion-icon :icon="bag"></ion-icon>
                <ion-label>Pedidos</ion-label>
                <ion-badge>{{ pedidosFinalizadosoRechazados.length }}</ion-badge>
            </ion-tab-button>
            <ion-tab-button tab="finanzas" href="/tabs/finanzas">
                <ion-icon :icon="cash"></ion-icon>
                <ion-label>Finanzas</ion-label>
            </ion-tab-button>
            <ion-tab-button tab="tiendas" href="/tabs/tiendas">
                <ion-icon :icon="storefront"></ion-icon>
                <ion-label>Tiendas</ion-label>
                
            </ion-tab-button>
        </ion-tab-bar>
    </ion-tabs>

</ion-page>
</template>

<style>

</style>

<script>
import {

    IonPage,
    IonTabBar,
    IonTabs,
    IonTabButton,
    IonBadge,
    IonIcon,
        IonLabel,
} from '@ionic/vue';
import {
    cash,
    storefront,
    home,
    bicycle,
    bag,
    calendar,
    personCircle
} from 'ionicons/icons';

export default {
    name: 'inicio',
    components: {
        IonPage,
        IonTabBar,
        IonTabs,
        IonTabButton,
        IonBadge,
        IonIcon,
        IonLabel,

    },
    setup() {
        return {
            bag,
            storefront,
            bicycle,
            cash,
            home,
            calendar,
            personCircle,
        }
    },
    computed: {
        pedidosFinalizadosoRechazados() {
            if(this.pedidos !== null ) {
            return this.pedidos.filter(function (item) {
                return item.estado == 'entregue el pedido' || item.estado == 'rechazado'
            });
            }
            return []
        },
        pedidos: {
            get() {
                return this.$store.state.pedidos;
            },
        },
        repartidores: {
            get() {
                return this.$store.state.repartidores;
            },
        }
    }

}
</script>
